@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.appBody{
  background-color:red;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Logo-navBar{
  border-radius: 20px;
}

/*CALCULATOR SECTION*****/

.calc {
    display: none;
}

.calcShow {
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    margin: 0 10%;
    width: 100%;
    min-height: 10vh;
}

/*NUMBERS*/

.numbers {
    margin-top: 2vh;
    width: 50%;
    cursor: pointer;
}

.numbersAnimation {
    animation: calcShowAnimation 4s ease-out forwards;
    
}

@keyframes calcShowAnimation{
    0%{margin-top:-15%; opacity:0}
    50%{margin-top:-15%; opacity:0}
    100%{margin-top:0; opacity:1}
}

.number {
	width:20%;
    min-height: 7vh;
    margin: 0.8vh 0.5vw;

    border: none;
    border-radius: 5px;
    outline: none;

	background-color: white;
    font-size: 2vw;
    font-weight: bold;

    box-shadow: 2px 2px 3px #000;
}

.number:active {
    box-shadow: inset 0 0 4px #000;
    
}

.ok {
    background: rgb(9, 93, 172);
    color:white;
}

.toast{
   margin-left: 25px;
}
.credit-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    width: 400px;
    height: 250px;
    padding: 25px;
    border-radius: 15px;

    color: white;

    background-image: linear-gradient(25deg, #0f509e, #1399cd);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}

.credit-card__number {
    display: flex;
    justify-content: center;
    font-size: 50px;
    font-family: 'Fjalla One', sans-serif;

}

.credit-card__number input {
    font-size: 2rem;
    font-family: 'Fjalla One', sans-serif;
    border-radius: 10px;
    background-color: rgb(26, 128, 192);
}

.btn-success{
    cursor: pointer;
}

.credit-card__info {
    display: flex;
    justify-content: space-between;

    font-family: 'Fjalla One', sans-serif;
    font-size: 25px;
}

.credit-card__info_label {
    font-size: 14px;
}

.credit-card__info_expiry {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.input{
    color:white;
    border:none;
}
.submitButton{
    margin-left:10%;
    padding:10px;
}

